





























import BASEURL from "../../common/baseInfo.js";
import wzpPicker from "../../components/wzp-picker/wzpPicker.vue"
export default {
	data() {
		return {
			pickerList: [{
					label: "男"
				},
				{
					label: "女"
				}
			],
			sexVal: '',
			nameVal: '',
			ageVal: null,
			introduceVal: '',
			priceVal: null,
			uerInfo: {},
			openId: '',
		};
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userInfo',
			success: function (res) {
				// console.log(JSON.parse(res));
				console.log(res.data);
				that.uerInfo = res.data;
			}
		})
		uni.getStorage({
			key: 'openIdInfo',
			success: function(res) {
				// console.log(JSON.parse(res));
				console.log(res);
				that.openId = res.data;
				that.getUserAppInfo();
			}
		})
	},
	components: {
		wzpPicker
	},
	methods: {
		getUserAppInfo() {
			let that = this;
			uni.request({
				url: `${BASEURL}/khxcx-0.0.1-SNAPSHOT/findUserByOpenId.action`,
				method: 'GET',
				data: {
					openId: that.openId
				},
				success: res => {
					console.log(res);
					that.ageVal = res.data.data.age;
					that.nameVal = res.data.data.givenName;
					that.sexVal = res.data.data.sex;
					that.introduceVal = res.data.data.introduce;
					that.priceVal = res.data.data.priece;
				},
				fail: (err) => {
					console.log(err);
				},
				complete: () => {}
			});
		},
		showEmpnoPicker: function(e) {
			this.$refs.wzpPicker.showPicker();
		},
		onCancle(res) {
			console.log(res);
		},
		onConfirm(res) {
			this.sexVal = this.pickerList[res[0]].label;
			console.log(this.sexVal);
			console.log(res);
		},
		submitFn() {
			let that = this;
			if (that.nameVal == '') {
				uni.showToast({
					title: '请填写真实姓名',
					icon: 'none',
					mask: false,
					duration: 2000
				});
				return;
			}
			if (that.sexVal == '') {
				uni.showToast({
					title: '请选择性别',
					icon: 'none',
					mask: false,
					duration: 2000
				});
				return;
			}
			if (that.ageVal == 0 || that.ageVal == null) {
				uni.showToast({
					title: '请填写年龄',
					icon: 'none',
					mask: false,
					duration: 2000
				});
				return;
			}
			if (that.priceVal == 0 || that.priceVal == null) {
				uni.showToast({
					title: '请填写个人价值',
					icon: 'none',
					mask: false,
					duration: 2000
				});
				return;
			}
			let submitData = {
				openId: that.openId,
				givenName: that.nameVal,
				age: that.ageVal,
				sex: that.sexVal,
				introduce: that.introduceVal,
				priece: that.priceVal
			}
			uni.request({
				url: `${BASEURL}/khxcx-0.0.1-SNAPSHOT/updateUserIfoByOpenId.action`,
				method: 'GET',
				data: submitData,
				success: res => {
					console.log(res);
					uni.showToast({
						title: '修改成功',
						mask: false,
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack();
					},2000)
				},
				fail: (err) => {
					console.log(err);
				},
				complete: () => {}
			});
		}
		
	}
}
